  color: var(--sjs-primary-forecolor, var(--primary-foreground, #fff));
  font-weight: bold;
}

.svc-json-editor-tab__content-errors {
  position: absolute;
  right: 0%;
  top: calc(0% + 3 * var(--sjs-base-unit, var(--base-unit, 8px)));
  max-width: calc(100% - 2 * var(--sjs-base-unit, var(--base-unit, 8px)));
